/*** Navbar ***/

nav {
  height: 50px;
  width: 100%;
}

.navbar {
  position: absolute;
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  background-color: greenyellow;
}

.nav_links {
  display: flex;
}

.navbar .nav_links ul {
}

/*** Navbar - Link ***/

.nav_links {
  position: relative;
  border: 1px solid blue;
}

.nav_links li {
  list-style-type: none;
}
.nav_links li a {
  margin: 15px;
  text-decoration: none;
  color: white;
}

.nav_links li a:hover {
  text-decoration: none;
  color: yellow;
}

.navbar .nav_links ul li {
  margin: 0 10px;
}

.navbar .nav_links ul li.active {
  color: blue;
}

/*** Lien dans la navbar ***/
.navlinks {
  margin-right: 10px;
}

/*** Navbar - Logo ***/

.nav_logo {
  width: 150px;
  height: 50px;
  border: 1px solid blue;
}

/*** Navbar - Right ***/
.rightNav {
  border: 1px solid gold;
  display: flex;
}

/*** Navbar - Left ***/
.leftNav {
  border: 1px solid pink;
}

/*** hamburger ***/

.menuToggler {
}

.menu {
}

/*** Media queries ***/

@media screen and (max-width: 800px) {
  .nav_links {
    display: none;
  }
}
